@font-face {
  font-family: DNFBitBitv2;
  font-style: normal;
  font-weight: 800;
  src: url("//cdn.df.nexon.com/img/common/font/DNFBitBitv2.otf") format("opentype");
}

:root {
  font-size: 16pt;
}

* {
  box-sizing: border-box;
}

html, body {
  color: #0ff;
  background: #000;
  width: 100vw;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Galmuri11, sans-serif;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5 {
  margin: 0;
  padding: 0;
  font-family: DNFBitBitv2, sans-serif;
}

section {
  background-color: #000;
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

section h2 {
  color: #fff;
  font-size: 3rem;
}

section h3 {
  font-size: 1.5rem;
}

section div.text, section div.content {
  z-index: 10;
  width: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#title-page {
  z-index: 90;
}

#title-page video.header-bg {
  z-index: 1;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#title-page:after {
  content: "";
  z-index: 1;
  pointer-events: none;
  background-color: #0009;
  position: absolute;
  inset: 0;
}

#title-page div.content {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#title-page div.content h1 {
  color: #fff;
  font-size: 1.5rem;
}

#title-page div.content h2 {
  text-shadow: -1px 0 1px #0ff, 1px 0 1px #0ff, 0 -1px 1px #0ff, 0 1px 1px #0ff, -2px 0 1px #0ff, 2px 0 1px #0ff, 0 -2px 1px #0ff, 0 2px 1px #0ff, -3px 0 1px #0ff, 3px 0 1px #0ff, 0 -3px 1px #0ff, 0 3px 1px #0ff, -4px 0 1px #0ff, 4px 0 1px #0ff, 0 -4px 1px #0ff, 0 4px 1px #0ff, -5px 0 1px #0ff, 5px 0 1px #0ff, 0 -5px 1px #0ff, 0 5px 1px #0ff, -6px 0 1px #0ff, 6px 0 1px #0ff, 0 -6px 1px #0ff, 0 6px 1px #0ff, -7px 0 1px #0ff, 7px 0 1px #0ff, 0 -7px 1px #0ff, 0 7px 1px #0ff, -8px 0 1px #0ff, 8px 0 1px #0ff, 0 -8px 1px #0ff, 0 8px 1px #0ff, -9px 0 1px #0ff, 9px 0 1px #0ff, 0 -9px 1px #0ff, 0 9px 1px #0ff, 0 0 30px #0ff;
  color: #000;
  font-size: 4rem;
  line-height: 4rem;
}

#title-page div.info {
  z-index: 10;
  color: #ff0;
  animation: .8s linear infinite blink;
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translate(-50%);
}

@keyframes blink {
  0% {
    opacity: 0;
    transform: translate(-50%, -10%);
  }

  50% {
    opacity: 1;
    transform: translate(-50%);
  }

  100% {
    opacity: 0;
    transform: translate(-50%, 10%);
  }
}

section#people-page {
  z-index: 80;
}

section#people-page div.video {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

section#people-page video {
  object-fit: contain;
  width: 100%;
  height: 100%;
}

section#people-page div.text > div {
  margin-bottom: 2rem;
}

section#execution-page {
  z-index: 70;
}

section#execution-page div.video {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

section#execution-page video#execution-video {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

section#date-hours-page {
  z-index: 60;
}

section#date-hours-page div.clock {
  border: 5px solid #fff;
  border-radius: 50%;
  width: min(80vw, 80vh);
  height: min(80vw, 80vh);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

section#date-hours-page div.clock div.clock-inner {
  width: 100%;
  height: 100%;
  position: relative;
}

section#date-hours-page div.clock div.hand {
  background: #f33;
  width: 5px;
  height: 45%;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%);
}

section#date-hours-page div.clock div.hand.hour {
  transform-origin: bottom;
  background: #00baff;
  width: 15px;
  height: 30%;
  animation: 360s linear infinite second-hand;
}

section#date-hours-page div.clock div.hand.minute {
  transform-origin: bottom;
  background: #0ff;
  width: 10px;
  height: 40%;
  animation: 30s linear infinite second-hand;
}

section#date-hours-page div.clock div.hand.second {
  transform-origin: bottom;
  animation: .5s linear infinite second-hand;
}

@keyframes second-hand {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

section#marble-count-page {
  z-index: 50;
}

section#marble-count-page h2 {
  white-space: nowrap;
  font-size: 3rem;
}

section#marble-count-page .content {
  z-index: 2;
}

section#marble-count-page .content div.showcase > div#containers {
  min-width: 100%;
  display: flex;
}

section#marble-count-page .content div.showcase > div#containers .container-wrap {
  flex-shrink: 0;
  width: 100px;
  height: 100px;
  overflow: visible;
}

section#marble-count-page .container-3d {
  perspective: 1000px;
  flex-grow: 0;
  flex-shrink: 0;
  width: 300px;
  height: 120px;
  transform: scale(.3);
}

section#marble-count-page .cube {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  position: relative;
  transform: rotateX(-15deg)rotateY(-120deg);
}

section#marble-count-page .face {
  color: #ff0;
  background: #000;
  border: 2px solid #ff0;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 36px;
  font-weight: bold;
  display: flex;
  position: absolute;
}

section#marble-count-page .front {
  transform: translateZ(40px);
}

section#marble-count-page .back {
  transform: rotateY(180deg)translateZ(40px);
}

section#marble-count-page .right {
  width: 80px;
  left: 110px;
  transform: rotateY(90deg)translateZ(150px);
}

section#marble-count-page .left {
  width: 80px;
  left: 110px;
  transform: rotateY(-90deg)translateZ(150px);
}

section#marble-count-page .right:after, section#marble-count-page .left:after {
  content: "";
  border: 2px solid #ff0;
  width: 33%;
  height: 100%;
  position: absolute;
  left: 33%;
}

section#marble-count-page .top {
  background: #ff0;
  height: 80px;
  top: 20px;
  transform: rotateX(90deg)translateZ(60px);
}

section#marble-count-page .bottom {
  background: #ff0;
  height: 80px;
  top: 20px;
  transform: rotateX(-90deg)translateZ(60px);
}

section#marble-count-page .front:before, section#marble-count-page .back:before {
  content: "";
  border: 2px solid #ff0;
  width: 20%;
  height: 100%;
  position: absolute;
  left: 60%;
}

section#marble-count-page .front:after, section#marble-count-page .back:after {
  content: "";
  border: 2px solid #ff0;
  width: 20%;
  height: 100%;
  position: absolute;
  left: 20%;
}

@keyframes rotate {
  0% {
    transform: scale(1)rotateY(0)rotateX(-10deg)rotateZ(-10deg);
  }

  50% {
    transform: scale(1)rotateY(180deg)rotateX(10deg)rotateZ(10deg);
  }

  100% {
    transform: scale(1)rotateY(360deg)rotateX(-10deg)rotateZ(-10deg);
  }
}

section#coffee-page {
  z-index: 40;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

section#thanks-page {
  z-index: 30;
}

section#thanks-page h2 {
  text-align: center;
}

section#thanks-page p.info {
  width: 90%;
  font-size: .7rem;
  position: absolute;
  bottom: 2rem;
  left: 5%;
}

@media screen and (width <= 800px) {
  :root {
    font-size: 8pt;
  }

  section:not(#title-page) div.content, section:not(#title-page) div.text {
    top: unset;
    background-color: #0006;
    margin-top: 2rem;
    padding: 1rem;
    display: block;
    transform: translate(-50%);
  }

  section:not(#title-page) div.content p, section:not(#title-page) div.text p {
    font-size: 13pt;
    font-weight: bold;
  }

  section:not(#title-page) div.content h1, section:not(#title-page) div.text h1 {
    font-size: 24px;
  }

  section:not(#title-page) div.content h2, section:not(#title-page) div.text h2 {
    font-size: 32px;
  }

  section#thanks-page:not(#title-page) > div.content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  section#title-page .content h1 {
    color: #0ff;
    font-size: 32px;
  }

  section#title-page .content h2 {
    text-align: center;
    opacity: .9;
    text-shadow: -1px 0 1px #0ff, 1px 0 1px #0ff, 0 -1px 1px #0ff, 0 1px 1px #0ff, -2px 0 1px #0ff, 2px 0 1px #0ff, 0 -2px 1px #0ff, 0 2px 1px #0ff, -3px 0 1px #0ff, 3px 0 1px #0ff, 0 -3px 1px #0ff, 0 3px 1px #0ff, -4px 0 1px #0ff, 4px 0 1px #0ff, 0 -4px 1px #0ff, 0 4px 1px #0ff, 0 0 30px #0ff;
    color: #000;
    font-size: 64px;
    line-height: 100%;
  }

  section#title-page p {
    color: #fff;
    font-size: 24px;
  }
}

div.filter {
  pointer-events: none;
  opacity: 1;
  z-index: 1000;
  background-color: #0000;
  background-image: repeating-linear-gradient(#0000, #0000 1px, #0009 2px, #0000 3px), repeating-linear-gradient(to right, #0000, #0000 1px, #0009 2px, #0000 3px);
  background-position: 0 0, 0 0;
  background-repeat: repeat, repeat;
  background-size: 3px 3px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  position: fixed;
  inset: 0;
}
